
@import "style/markdown.css";
body,
html {
  margin: 0 auto;
  padding: 0;
  max-width: 64rem;
  height: 100%;
  font-size: 0.32rem;
  background-color: #e1e1e1;
}
h1,
h3,
h4,
h5,
p,
a,
img {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  color: #000;
  text-decoration: none;
}
button,
input {
  outline: none;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  -webkit-tap-highlight-color: transparent;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.orange {
  color: #80bd01;
}
.container {
  margin: .5rem .05rem .5rem .05rem;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
}
.container .path {
  padding-left: .4rem;
  height: 0.74666667rem;
  line-height: 0.74666667rem;
  font-size: 0.32rem;
  background-color: #f6f6f6;
  color: #999;
}
#app {
  padding: 0 .05rem;
}
#app header {
  margin: 0;
  background-color: #444;
}
#app header h1 {
  text-align: center;
}
#app header h1 a {
  height: 1.6rem;
  line-height: 1.6rem;
}
#app header h1 a img {
  width: 2.66666667rem;
  vertical-align: middle;
}
#app header .search {
  text-align: center;
}
#app header .search input {
  width: 3.46666667rem;
  height: 0.26666667rem;
}
#app header nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 0 .4rem;
  line-height: 0.8rem;
  font-size: 0.37333333rem;
}
#app header nav a {
  color: #c8c6c8;
}



